body {
  background-color: #3e3529;
  color: #ffff00;
  font-family: "RuneScape UF", sans-serif;
  font-weight: 600;
  text-align: center;
  font-size: 20px;
}

.taskContainer {
  padding: 20px;
  border: 1px solid yellow;
  max-width: 500px;
  margin: 50px 0;
}

.taskContainer > div {
  padding: 10px;
}

.allTasks {
  display: flex;
  justify-content: space-between;
  max-width: 50vw;
  margin: auto;
}

input[type="checkbox"] {
  position: relative;
  cursor: pointer;
  margin: 20px;
  background-color: #ffff00;
  color: #000;
}

input[type="checkbox"]:before {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  top: 50%;
  left: 50%;
  background-color: inherit;
  outline: 1px rgba(255, 255, 255, 0.2) solid;
  transform: translate(-50%, -50%);
}

input[type="checkbox"]:checked:before {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  top: 50%;
  left: 50%;
  background-color: inherit;
  outline: 1px rgba(255, 255, 255, 0.2) solid;
  transform: translate(-50%, -50%);
  box-shadow: 0px 0px 4px 1px #fff;
}

input[type="checkbox"]:checked:after {
  content: "";
  display: block;
  width: 6px;
  height: 10px;
  border: solid;
  border-width: 0 2px 2px 0;
  border-color: inherit;
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}
